/* ============================== 767 ================================== */

@media (max-width: 767.98px) {

  /* section title */

  .section-title {
    margin-bottom: 10px;
    font-size: 30px; }

  /* section subtitle*/
  .section-subtitle {
    font-size: 14px; }

  .hidden-mobile {
    display: none; }

  .btn {
    display: block; }

  /* Slick dots */

  .slick-dots li {
    button {
      font-size: 15px;
      width: 15px;
      height: 15px;

      &:before {
        width: 15px;
        height: 15px;
        font-size: 15px; } } }

  /* Кнопка Menu */

  .menu {

    &-btn {
      border-radius: 5px;
      padding: 2px;
      display: flex;
      justify-content: center;
      align-items: center;
      cursor: pointer;
      position: fixed;
      z-index: 3;
      top: 50px;
      left: auto;
      right: 50px;
      width: 40px;
      height: 40px;
      border: 1px solid #555;

      &__elem, &__elem:after, &__elem:before {
        display: inline-block;
        height: 2px;
        background-color: $active; }

      &__elem {
        position: relative;
        width: 80%;

        &:after, &:before {
          width: 100%;
          position: absolute;
          content: '';
          transition: all 0.3s; }

        &:after {
          top: 9px; }

        &:before {
          bottom: 9px; } }

      &.active &__elem {

        background-color: transparent;

        &:before, &:after {
          top: 0;
          background-color: lighten($active, 20); }

        &:before {
          transform: rotate(135deg); }

        &:after {
          transform: rotate(-135deg); } } } }


  /* Header */

  .header {
    justify-content: center;

    &__logo {
      flex-basis: auto;
      max-width: none; }

    &__menu {
      margin-left: 0;
      margin-right: 0;
      flex-basis: 100%;
      max-width: 100%; }

    &-menu {
      display: none;
      flex-direction: column;
      justify-content: center;
      position: fixed;
      top: 0;
      left: 0;
      width: 100vw;
      height: 100vh;
      margin: 0;
      background-color: rgba(darken($active, 20), 0.8);

      &.active {
        display: flex;
        z-index: 2; }

      &__item {
        margin-bottom: 1rem;

        &:last-child {
          margin-bottom: 0; } }

      &__link {
        font-size: 35px;
        font-weight: 700;
        letter-spacing: 2px;
        line-height: normal; } } }

  /* Home */

  .home {

    &-slider {
      &__btn {
        max-width: 180px;
        margin-left: auto;
        margin-right: auto;

        &:first-child {
          margin-right: auto;
          margin-bottom: 15px; } } } }


  /* portfolio */

  .portfolio {
    padding-top: 30px;
    padding-bottom: 30px;

    &__slider {
      margin-top: 30px; }

    &-slider {

      &-item {

        &__btns {
          margin-bottom: 30px; }

        &__btn {
          display: block;
          max-width: 190px;
          margin-left: auto;
          margin-right: auto;
          font-size: 14px;

          &:first-child {
            margin-right: auto;
            margin-bottom: 15px; } } } } }


  /* About */

  .about {
    padding-bottom: 20px;

    &-info {
      &-wrap {
        flex-direction: column;
        justify-content: center; }

      &-block {
        margin-bottom: 25px;
        max-width: 220px;
        min-height: 220px; } } }

  /* features */

  .features {
    padding-top: 30px;
    padding-bottom: 30px;

    .section-subtitle {
      max-width: 420px;
      margin-left: auto;
      margin-right: auto;
      line-height: 24.18px; }

    &-wrap {
      margin-top: 45px;
      justify-content: space-around; }

    &__block {
      flex-basis: 80%; } }


  /* Start */

  .start {
    padding-top: 35px;
    padding-bottom: 37px;

    &__title, &__text {
      font-size: 20px; }

    &__title {
      margin-bottom: 17px; }

    &__text {
      max-width: 550px;
      line-height: 35px; }

    &__btn {
      max-width: 187px;
      margin-left: auto;
      margin-right: auto;
      font-size: 15px; } }

  /* themes */

  .themes {
    padding-bottom: 40px;

    .section-title {
      max-width: none; }

    &__content {
      flex-basis: 90%; } }


  /*contact */

  .contact {

    &__title {
      width: 100%;
      height: 50px;
      margin-top: -25px;
      font-size: 22px;
      line-height: 50px; }

    &__links {
      margin-top: 30px;
      margin-bottom: 17px; }

    &__copy {
