    width: 15px;
    height: 15px;
  }
  .slick-dots li button:before {
    width: 15px;
    height: 15px;
    font-size: 15px;
  }
  /* Кнопка Menu */
  .menu-btn {
    border-radius: 5px;
    padding: 2px;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
    position: fixed;
    z-index: 3;
    top: 50px;
    left: auto;
    right: 50px;
    width: 40px;
    height: 40px;
    border: 1px solid #555;
  }
  .menu-btn__elem, .menu-btn__elem:after, .menu-btn__elem:before {
    display: inline-block;
    height: 2px;
    background-color: #f9812b;
  }
  .menu-btn__elem {
    position: relative;
    width: 80%;
  }
  .menu-btn__elem:after, .menu-btn__elem:before {
    width: 100%;
    position: absolute;
    content: '';
    transition: all 0.3s;
  }
  .menu-btn__elem:after {
    top: 9px;
  }
  .menu-btn__elem:before {
    bottom: 9px;
  }
  .menu-btn.active .menu-btn__elem {
    background-color: transparent;
  }
  .menu-btn.active .menu-btn__elem:before, .menu-btn.active .menu-btn__elem:after {
    top: 0;
    background-color: #fcbc8e;
  }
  .menu-btn.active .menu-btn__elem:before {
    transform: rotate(135deg);
  }
  .menu-btn.active .menu-btn__elem:after {
